body {
    background-color: #ccc;
}

.App {
    padding: 3em;
    width: 800px;
    margin: 3em auto;
}

.MuiGrid-root.control {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: calc(10px + 2vmin);
}
.song,
.album-side {
    width: 350px;
}

.album-side {
    background-color: hsla(24, 78%, 50%, 50%);
}

.album-side.in-error {
    background-color: hsla(0, 78%, 50%, 75%);
}

.MuiListItemButton-root.song {
    background-color: hsl(24, 78%, 50%, 25%);
}

.MuiListItemButton-root.song:hover {
    background-color: hsl(24, 78%, 50%, 100%);
}

.MuiListItemButton-root.song.Mui-selected {
    background-color: hsl(24, 78%, 50%, 80%);
}

.song span,
.album-side span {
    vertical-align: middle;
}

.song .name {
    font-size: 0.8em;
}

.song .duration,
.album-side .duration {
    font-size: 0.675em;
    font-family: 'Courier New', Courier, monospace;
}

.album-side .container,
.song .container {
    width: 100%;
}

.album-side .flex-row,
.song .flex-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.album-side .error-message {
    font-size: 0.7em;
}

.MuiListItemButton-root.song.Mui-disabled {
    color: black;
}
